import {MeshBasicMaterial} from 'three'
import {getTexture, textureTypes} from './Textures'
import {materialTypes} from './MaterialTypes'

const materialColors = {
    default: '#ff00ff',
    defaultShadow: '#ffffff',
    defaultLine: '#000000',

    green: '#1aae19',
    cyan: '#08eafc',
    blue: '#003fff',
    pink: '#ff00ff',
    black: '#000000',
    orange: '#ff7500',
    turquoise: '#a9ecea',
    grey: '#666666',
    white: '#ffffff',

    selectionArea: '#b0b0b0',
}

const materials = {
    background: new MeshBasicMaterial({map: getTexture(textureTypes.background)}),
    transparent: new MeshBasicMaterial({name: materialTypes.transparent, visible: false}),
    selectionArea: new MeshBasicMaterial({
        name: materialTypes.selectionArea,
        color: materialColors.selectionArea,
        transparent: true,
        opacity: .3
    }),
    pink: new MeshBasicMaterial({name: materialTypes.pink, color: materialColors.pink}),
    white: new MeshBasicMaterial({name: materialTypes.white, color: materialColors.white}),
    black: new MeshBasicMaterial({name: materialTypes.black, color: materialColors.black}),
    green: new MeshBasicMaterial({name: materialTypes.green, color: materialColors.green}),
    cyan: new MeshBasicMaterial({name: materialTypes.cyan, color: materialColors.cyan}),
    blue: new MeshBasicMaterial({name: materialTypes.blue, color: materialTypes.blue}),
    orange: new MeshBasicMaterial({name: materialTypes.orange, color: materialColors.orange}),
    default: new MeshBasicMaterial({
        name: materialTypes.default,
        color: materialColors.default,
        visible: true,
        depthWrite: true
    })
}

export function getMaterial (materialType) {
    return materials[materialType]
}
