import {withStyles} from "@material-ui/core";
import {compose} from "redux";
import {withTranslation} from "react-i18next";
import Expandable from "./common/Expandable";
import ColoredIconButton from "./common/buttons/ColoredIconButton";
import BottomSpreadsheetExpandedContent from "./navigation/BottomSpreadsheetExpandedContent";
import PropTypes from "prop-types";
import UndoRedo from "./navigation/UndoRedo";
import SpreadsheetActions from "./navigation/SpreadsheetActions";

const styles = theme => ({
    root: {
        background: theme.colors.palette.neutral.grayLight,
        minHeight: theme.skeleton.bottomSpreadsheet.height,
        borderRadius: '30px 30px 0 0',
        boxShadow: theme.colors.shadows.spreadsheet,
        border: 'solid 1px ' + theme.colors.palette.neutral.grayMin,
        padding: theme.skeleton.bottomSpreadsheet.padding,
        boxSizing: 'border-box',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'space-between',
        position: 'absolute',
        bottom: 0,
        width: '100vw'
    },
    actions: {
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center'
    }
})

function BottomSpreadsheet({classes, t, expandable, expandedContent, ...props}) {
    return (
        <div className={classes.root}>
            {expandable &&
            <Expandable>
                <BottomSpreadsheetExpandedContent content={expandedContent}/>
            </Expandable>}
            <SpreadsheetActions
                leftComponent={<UndoRedo undoClick={() => console.log('undo')} redoClick={() => console.log('redo')}/>}
                rightComponent={<ColoredIconButton text={t('continue')} halfWidth/>}/>
        </div>
    )
}

BottomSpreadsheet.propTypes = {
    expandable: PropTypes.bool,
    expandedContent: PropTypes.element
}

export default compose(withStyles(styles), withTranslation())(BottomSpreadsheet)