import {removeAllChildrenFromParent} from './GroupHelper'
import {getStampConfigByRepresentationType, StampGeometry} from '../geometries/StampGeometry'

let previewStamp = null


export class PreviewStamp extends StampGeometry {

    constructor(scene) {
        super(scene)
        const config = getStampConfigByRepresentationType()

        this.name = 'previewStamp'
        this.config = config

        previewStamp = this
    }

    updateScale(scale) {
        this.scale.y = scale
    }

    updateStamp(representationType, position, angle) {
        if (this.face) {
            removeAllChildrenFromParent(this.face)
            this.face.dispose()
            this.remove(this.face)
        }

        super.updateStamp(representationType, position, angle)
    }

    updateDepth(depth) {
        if (this.config.depth !== depth) {
            this.config.depth = depth
            this.updateStamp(this.representationType)
        }
    }

    updatePosition(position) {
        this.position.set(position.x, position.y)
    }

    updateRotation(angle) {
        if (this.face) {
            this.face.rotation.z = 0
            this.face.rotateZ(angle)
        }
    }


}

export function setPreviewStampActive(position, representationType, angle = null, depth) {
    if (![].includes(representationType)) {
        previewStamp.setActive(false)
        return
    }

    previewStamp.setActive(true)

    if (previewStamp.representationType !== representationType) {
        previewStamp.updateStamp(representationType, position, angle)
    } else if (previewStamp) {
        previewStamp.updateDepth(depth)
        previewStamp.updatePosition(position)
        previewStamp.updateRotation(angle)
    }
}
