import {Geometry} from './Geometry'
import {representationTypes} from '../config/RepresentationTypes'
import {doorDecoGeometry, Face,} from './FaceFactory'
import {Mesh} from 'three'
import {getMaterial} from '../common/Materials'
import {getVerticesByWidthAndHeight} from '../helper/GeometryHelper'
import {materialTypes} from '../common/MaterialTypes'

export const stampActions = {
    drag: 'drag',
}

export class StampGeometry extends Geometry {

    constructor(group, representationType, geometryActions = {}) {
        super(group, representationType, Object.assign(stampActions, geometryActions))

        this.config = getStampConfigByRepresentationType(representationType)

        this.setActive(true)
        this.setAction(this.actions.idle)
    }

    updateStamp(representationType, position, angle) {
        const vertices = getVerticesByWidthAndHeight(this.config.width, this.config.depth)

        this.face = new Face(representationType, vertices)
        this.face.rotation.z = 0
        this.face.position.z = 0.03
        this.face.setActive(true)
        this.add(this.face)

        if (this.config.deco && this.config.deco.length) {
            this.setDecorationMeshes(this.config.deco)
        }

        if (angle) {
            this.face.rotateZ(angle)
        }

        if (position) {
            this.position.set(position.x, position.y)
        }

        this.representationType = representationType
    }

    setDecorationMeshes(decorations) {
        decorations.forEach(deco => {
            const {geometry, material} = deco
            const mesh = new Mesh(geometry, material)

            this.face.add(mesh)
        })
    }

    moveDelta(movement) {
        if (movement.lengthSq() === 0)
            return

        this.face.moveDelta(movement)
    }

    setActive(active) {
        if (this.face) {
            this.face.setActive(active)

            this.face.children.forEach(deco => deco.visible = active)
        }
        this.setAction(active ? this.actions.select : this.actions.idle)
    }


}

export function getStampConfigByRepresentationType(representationType) {
    switch (representationType) {
        case representationTypes.door:
            return {
                width: 0.9,
                depth: 0.2,
                deco: [{
                    geometry: doorDecoGeometry,
                    material: getMaterial(materialTypes.black),
                }],
            }
        case representationTypes.window:
            return {
                width: 0.9,
                depth: 0.2,
            }
        default:
            return {
                width: 0.9,
                depth: 0.2,
            }

    }
}