import {Group} from 'three'
import {Vertex} from '../geometries/VertexFactory'
import {Edge} from '../geometries/EdgeFactory'

let magicGuideLine = null

const orientationTypes = {
    vertical: 'vertical',
    horizontal: 'horizontal',
    verHor: 'verHor',
}

export class MagicGuideLine extends Group {

    constructor(scene) {
        super()

        this.representationType = 'magicGuideLine'

        const startVertexVertical = new Vertex(1, this.representationType, {x: 100000, y: 0, z: 0})
        const endVertexVertical = new Vertex(1, this.representationType, {x: -100000, y: 0, z: 0})
        this.vertical = new Edge(startVertexVertical, endVertexVertical, 1, this.representationType)
        this.vertical.name = 'verticalMagicGuideLine'
        this.vertical.setActive(false)
        this.add(this.vertical)

        const startVertexHorizontal = new Vertex(1, this.representationType, {x: 0, y: 100000, z: 0})
        const endVertexHorizontal = new Vertex(1, this.representationType, {x: 0, y: -100000, z: 0})
        this.horizontal = new Edge(startVertexHorizontal, endVertexHorizontal, 1, this.representationType)
        this.horizontal.name = 'horizontalMagicGuideLine'
        this.horizontal.setActive(false)
        this.add(this.horizontal)

        scene.add(this)

        this.name = 'magicGuideLines'

        magicGuideLine = this
    }

    updateVertical(position) {
        this.vertical.update({x: position.x, y: 100000, z: 0}, {x: position.x, y: -100000, z: 0})
        this.vertical.setActive(true)
    }

    updateHorizontal(position) {
        this.horizontal.update({x: 100000, y: position.y, z: 0}, {x: -100000, y: position.y, z: 0})
        this.horizontal.setActive(true)
    }

    setOrientation(orientationType, position) {

        switch (orientationType) {
            case orientationTypes.vertical:
                this.updateVertical(position)
                this.horizontal.setActive(false)
                break
            case orientationTypes.horizontal:
                this.vertical.setActive(false)
                this.updateHorizontal(position)
                break
            case orientationTypes.verHor:
                this.updateVertical(position)
                this.updateHorizontal(position)
                break
            default:
                this.vertical.setActive(false)
                this.horizontal.setActive(false)
                break
        }
    }

    updateScale(scale) {
        this.vertical.updateScale(scale)
        this.horizontal.updateScale(scale)
    }
}

export function getSnappingPosition(event, position, otherPositions, autoSnap = false, snapDistance = 0.5) {
    if (autoSnap && (event.ctrlKey || event.metaKey)) {
        return position
    } else if (autoSnap || (event.ctrlKey || event.metaKey)) {
        return getNearestVerticesValuesAsPosition(position, otherPositions, snapDistance)
    } else {
        return position
    }
}

function getNearestVerticesValuesAsPosition(position, otherPositions, snapDistance) {
    let nearestVerticalDistance = Number.MAX_VALUE
    let nearestVerticalPosition = null
    let nearestHorizontalDistance = Number.MAX_VALUE
    let nearestHorizontalPosition = null
    let orientation = null

    otherPositions
        .forEach(otherPosition => {
            const verticalDistance = Math.abs(position.x - otherPosition.x)
            if (verticalDistance < nearestVerticalDistance) {
                nearestVerticalDistance = verticalDistance
                nearestVerticalPosition = otherPosition
            }

            const horizontalDistance = Math.abs(position.y - otherPosition.y)
            if (horizontalDistance < nearestHorizontalDistance) {
                nearestHorizontalDistance = horizontalDistance
                nearestHorizontalPosition = otherPosition
            }
        })

    if (nearestHorizontalPosition && nearestHorizontalDistance < snapDistance &&
        Math.abs(nearestHorizontalPosition.y - position.y) < snapDistance) {
        position.y = nearestHorizontalPosition.y
        orientation = orientationTypes.horizontal
    }

    if (nearestVerticalPosition && nearestVerticalDistance < snapDistance &&
        Math.abs(nearestVerticalPosition.x - position.x) < snapDistance) {
        position.x = nearestVerticalPosition.x
        orientation = orientation ? orientationTypes.verHor : orientationTypes.vertical
    }

    if (magicGuideLine) {
        magicGuideLine.setOrientation(orientation, position)
    }

    return position
}