export function isPolygonClockwise(vertices) {
    let result = 0

    for (let i = 0; i < vertices.length - 1; i++) {
        result += getResult(vertices[i], vertices[i + 1])
    }

    result += getResult(vertices[vertices.length - 1], vertices[0])

    return result >= 0
}

function getResult(startVertex, endVertex) {
    return (endVertex.x - startVertex.x) * (endVertex.y + startVertex.y)
}

export function getVerticesByWidthAndHeight(width, height) {
    const vertices = []

    vertices.push({x: -width / 2, y: height / 2})
    vertices.push({x: width / 2, y: height / 2})
    vertices.push({x: width / 2, y: -height / 2})
    vertices.push({x: -width / 2, y: -height / 2})

    return vertices
}

export function isRectangleContainsVertex(leftTop, rightBottom, vertex) {
    return leftTop.x <= vertex.x && vertex.x <= rightBottom.x &&
        rightBottom.y <= vertex.y && vertex.y <= leftTop.y
}

export function isLinesIntersecting(line_0, line_1) {
    const x1 = line_0.startVertex.x
    const y1 = line_0.startVertex.y
    const x2 = line_0.endVertex.x
    const y2 = line_0.endVertex.y

    const x3 = line_1.startVertex.x
    const y3 = line_1.startVertex.y
    const x4 = line_1.endVertex.x
    const y4 = line_1.endVertex.y

    const uA = ((x4 - x3) * (y1 - y3) - (y4 - y3) * (x1 - x3)) / ((y4 - y3) * (x2 - x1) - (x4 - x3) * (y2 - y1))
    const uB = ((x2 - x1) * (y1 - y3) - (y2 - y1) * (x1 - x3)) / ((y4 - y3) * (x2 - x1) - (x4 - x3) * (y2 - y1))

    return 0 <= uA && uA <= 1 && 0 <= uB && uB <= 1
}