import {PolyEdgeController} from './PolyEdgeController'
import {geometryTypes} from '../config/GeometryTypes'
import {createGeometryByRepresentationType} from '../factories/GeometryFactory'


export class PolygraphController extends PolyEdgeController {

    onLeftMouseDown(event, geometry, currentGeometries) {
        if (!geometry) {
            return this.onLeftMouseDownNew(event, currentGeometries)
        } else {
            return this.onLeftMouseDownEdit(event, geometry, currentGeometries)
        }
    }

    onLeftMouseDownNew(event, currentGeometries) {
        const childGeometry = this.floorPlanerController.getRaycastHitObjectByRepresentationTypesAndGeometryType(
            [this.representationType], [geometryTypes.vertex, geometryTypes.edge])
        const childGeometryType = childGeometry ? childGeometry.geometryType : null

        switch (childGeometryType) {
            case geometryTypes.vertex:
                const parentGeometryFromVertex = childGeometry.getGeometry()
                parentGeometryFromVertex.extendAtVertex(childGeometry, this.getCurrentPosition(event))
                currentGeometries.push(parentGeometryFromVertex)
                return true
            case geometryTypes.edge:
                const parentGeometryFromEdge = childGeometry.getGeometry()
                parentGeometryFromEdge.extendAtEdge(childGeometry, this.getCurrentPosition(event))
                currentGeometries.push(parentGeometryFromEdge)
                return true
            default:
                const polygraph = createGeometryByRepresentationType(this.representationType, this.group)
                return super.onLeftMouseDownNew(event, polygraph, currentGeometries)
        }


    }

    onLeftMouseDownEdit(event, polygraph, currentGeometries) {
        if (!polygraph)
            return false

        switch (polygraph.getAction()) {
            case polygraph.actions.addVertex:
                if (currentGeometries.length !== 1)
                    return false

                const childGeometry = this.floorPlanerController.getRaycastHitObjectByRepresentationTypesAndGeometryType(
                    [this.representationType], [geometryTypes.vertex, geometryTypes.edge], [], [polygraph.getLastVertex(), polygraph.getLastEdge()])
                const childGeometryType = childGeometry ? childGeometry.geometryType : null

                const currentPosition = this.getCurrentPosition(event, polygraph.getLastVertex().position)

                switch (childGeometryType) {
                    case geometryTypes.vertex:
                        const parentGeometryFromVertex = childGeometry.getGeometry()

                        if (parentGeometryFromVertex === polygraph) {
                            polygraph.connectLastVertexToOwnVertex(childGeometry)
                            this.removeGeometryFromCurrentGeometries(polygraph, currentGeometries)
                        } else {
                            parentGeometryFromVertex.mergeToOwnVertex(childGeometry, polygraph)
                            this.removeGeometryFromCurrentGeometries(polygraph, currentGeometries)
                        }
                        return true
                    case geometryTypes.edge:
                        const parentGeometryFromEdge = childGeometry.getGeometry()

                        if (parentGeometryFromEdge === polygraph) {
                            polygraph.connectLastVertexToOwnEdge(childGeometry)
                            this.removeGeometryFromCurrentGeometries(polygraph, currentGeometries)
                        } else {
                            parentGeometryFromEdge.mergeToOwnEdge(childGeometry, polygraph)
                            this.removeGeometryFromCurrentGeometries(polygraph, currentGeometries)
                        }
                        return true
                    default:
                        break
                }

                polygraph.addVertex(currentPosition)
                return true
            case polygraph.actions.select:
                return this.onLeftMouseDownSelect(event, polygraph)
            default:
                return false
        }
    }

    onLeftMouseDownSelect(event, polygraph) {
        const raycastHitObject = this.floorPlanerController.getRaycastHitObjectForGeometryByGeometryTypes(polygraph,
            [geometryTypes.vertex, geometryTypes.edge])

        if (!raycastHitObject)
            return false

        switch (raycastHitObject.geometryType) {
            case geometryTypes.vertex:
                polygraph.setDraggedVertex(raycastHitObject)
                polygraph.setAction(polygraph.actions.dragVertex)
                return true
            case geometryTypes.edge:
                polygraph.addVertexToEdge(raycastHitObject, this.getCurrentPosition(event))
                polygraph.setAction(polygraph.actions.dragVertex)
                return true
            default:
                return false
        }
    }

}