import {GeometryController} from './GeometryController'
import {getSnappingPosition} from '../helper/MagicGuideLine'
import {setPreviewVertexActive} from '../helper/PreviewVertex'
import {geometryTypes} from '../config/GeometryTypes'

export class PolyEdgeController extends GeometryController {

    onLeftMouseDownNew(event, geometry, currentGeometries) {
        const currentPosition = this.getCurrentPosition(event, geometry.draggedVertex ? geometry.draggedVertex.position : null)

        geometry.addVertex(currentPosition)
        geometry.addVertex(currentPosition)

        geometry.setAction(geometry.actions.addVertex)

        currentGeometries.push(geometry)

        return true
    }

    onMouseMove(event, geometry, currentGeometries) {
        const result = this.onMouseMovePreviewVertex(event, geometry, currentGeometries)

        if (result !== undefined)
            return result

        if (!geometry)
            return false

        return this.onMouseMoveCurrentGeometry(event, geometry, currentGeometries)
    }

    onMouseMovePreviewVertex(event, geometry, currentGeometries) {
        if (!geometry && !currentGeometries.length) {
            setPreviewVertexActive(this.getCurrentPosition(event), this.representationType)
            return true
        } else if (!!currentGeometries.length && geometry && geometry.getAction() === geometry.actions.select) {
            const raycastHitObject = this.floorPlanerController.getRaycastHitObjectForGeometryByGeometryTypes(geometry, [geometryTypes.vertex, geometryTypes.edge])

            if (raycastHitObject) {
                switch (raycastHitObject.geometryType) {
                    case geometryTypes.vertex:
                        return false
                    case geometryTypes.edge:
                        setPreviewVertexActive(this.getCurrentPosition(event), this.representationType)
                        return true
                    default:
                        return false
                }
            }
        }

        return undefined
    }

    onMouseMoveCurrentGeometry(event, geometry, currentGeometries) {
        switch (geometry.getAction()) {
            case geometry.actions.addVertex:
                if (currentGeometries.length !== 1)
                    return false

                const lastVertex = geometry.getLastVertex()
                geometry.updateVertex(this.getCurrentPosition(event, lastVertex.position), lastVertex)
                return true
            case geometry.actions.drag:
                geometry.moveDelta(this.floorPlanerController.getMouseDeltaMovement())
                return true
            case geometry.actions.dragVertex:
                geometry.updateVertex(this.getCurrentPosition(event, geometry.draggedVertex.position))
                return true
            default:
                return false
        }
    }

    onLeftMouseDoubleClick(event, geometry, currentGeometries, minVerticesCount) {
        if (!geometry)
            return false

        switch (geometry.getAction()) {
            case geometry.actions.addVertex:
                if (geometry.getVerticesCount() >= minVerticesCount) {
                    geometry.close()
                    this.removeGeometryFromCurrentGeometries(geometry, currentGeometries)
                    return true
                }
                return false
            default:
                return false
        }
    }

    onLeftMouseUp(event, geometry, currentGeometries) {
        if (!geometry) {
            return false
        }

        switch (geometry.getAction()) {
            case geometry.actions.drag:
                geometry.setAction(geometry.actions.select)
                const result = !!this.startDragPosition && !!this.startDragPosition.distanceToSquared(this.floorPlanerController.getCurrentPosition())
                this.startDragPosition = null
                return result
            case geometry.actions.dragVertex:
                geometry.setAction(geometry.actions.select)
                return true
            default:
                return false
        }
    }

    onBackspaceDown(event, geometry, currentGeometries) {
        if (!geometry)
            return false

        switch (geometry.getAction()) {
            case geometry.actions.addVertex:
                if (geometry.getVerticesCount() <= 2) {
                    this.removeGeometryFromGroup(geometry, currentGeometries)
                } else {
                    geometry.removeLastVertex()
                    geometry.updateVertex(this.getCurrentPosition(event, geometry.getLastVertex().position), geometry.getLastVertex())
                }
                return true
            case geometry.actions.select:
                this.removeGeometryFromGroup(geometry, currentGeometries)
                return true
            default:
                return false
        }
    }

    onEscapeDown(event, geometry, currentGeometries) {
        if (!geometry)
            return false

        switch (geometry.getAction()) {
            case geometry.actions.addVertex:
                this.removeGeometryFromGroup(geometry, currentGeometries)
                return true
            case geometry.actions.select:
                geometry.setActive(false)
                this.removeGeometryFromCurrentGeometries(geometry, currentGeometries)
                return true
            default:
                return false
        }
    }

    onEnterDown(event, geometry, currentGeometries, minVerticesCount = 2) {
        if (!geometry)
            return

        switch (geometry.getAction()) {
            case geometry.actions.addVertex:
                if (geometry.getVerticesCount() >= minVerticesCount) {
                    geometry.close()
                    this.removeGeometryFromCurrentGeometries(geometry, currentGeometries)
                    return true
                } else {
                    return false
                }
            default:
                return false
        }
    }

    getCurrentPosition(event, ignoreVertex = null) {
        const otherPositions = this.floorPlanerController.getAllVerticesPositions().filter(vertex => vertex !== ignoreVertex)

        return getSnappingPosition(event, this.floorPlanerController.getCurrentPosition(), otherPositions)
    }
}