import {withStyles} from "@material-ui/core";
import {compose} from "redux";
import {withTranslation} from "react-i18next";
import React from "react";
import BottomSpreadsheet from "../BottomSpreadsheet";
import PropTypes from "prop-types";

const styles = theme => ({
    root: {
        height: '100%',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'space-between'
    },
})

function SpreadsheetWrapper({classes, t, spreadsheetExpandable, expandedContent, ...props}) {
    return (
        <div className={classes.root}>
            <div className={classes.root}>
                {props.children}
            </div>
            <BottomSpreadsheet expandable={spreadsheetExpandable} expandedContent={expandedContent}/>
        </div>
    )
}

SpreadsheetWrapper.propTypes = {
    spreadsheetExpandable: PropTypes.bool,
    expandedContent: PropTypes.element
}

export default compose(withStyles(styles), withTranslation())(SpreadsheetWrapper)