import {withStyles} from "@material-ui/core";
import {compose} from "redux";
import {withTranslation} from "react-i18next";
import PropTypes from "prop-types";
import clsx from "clsx";

const styles = theme => ({
    root: {
        boxSizing: 'border-box',
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center'
    },
})

function SpreadsheetActions({classes, t, leftComponent, rightComponent, className, ...props}) {
    return (
        <div className={clsx(classes.root, className)}>
            {leftComponent ? leftComponent : <div/>}
            {rightComponent ? rightComponent : <div/>}
        </div>
    )
}

SpreadsheetActions.propTypes = {
    leftComponent: PropTypes.element,
    rightComponent: PropTypes.element
}

export default compose(withStyles(styles), withTranslation())(SpreadsheetActions)