const styles = theme => ({
    floorPlaner: {
        width: '100%',
        height: '100%',
    },
    canvasContaienr: {
        width: '100%',
        height: '100%',
    },
})

function FloorPlaner(props) {
    const mount = useRef(null)
    const rendererRef = useRef(null)
    const cameraRef = useRef(null)
    const {classes} = props

    useEffect(() => {
        const {scene, camera, raycaster, orbitControls, renderer, frameId} = setUp(mount)

        rendererRef.current = renderer
        cameraRef.current = camera

        const controller = new FloorPlanerController(mount, scene, camera, raycaster, orbitControls,
            [representationTypes.outline, representationTypes.wall, representationTypes.door, representationTypes.window, representationTypes.graph],
            renderer)

        controller.setGeometries(geometries)

        const currentMount = controller.mount.current

        return () => {
            controller.deconstructor(currentMount)
            cleanUp(currentMount, renderer, frameId)
        }
    }, [rendererRef, cameraRef])

    return (
        <Measure
            bounds
            onResize={() => {
                resize(mount, rendererRef.current, cameraRef.current)
            }}>
            {({measureRef}) => (
                <div className={classes.floorPlaner} ref={measureRef}>
                    <div className={classes.canvasContaienr} ref={mount} tabIndex={0}/>
                </div>
            )}
        </Measure>
    )
}

export default withStyles(styles)(FloorPlaner)
